    border: 4px solid rgba(255, 255, 255, 0.3);
    border-top: 4px solid #3498db;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    animation: spin 1s linear infinite;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -20px;
    margin-top: -20px;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;

    padding-top:20px;
}

form {
    font-size: 1.5rem;
    display:flex;
    align-items: center;
    justify-content: space-evenly;
    width:400px;
}

button {
    height:1.5rem;
    }

input {
    height:1.5rem;

}

.weather-app {
    max-width: fit-content;
    display: grid;
    grid-template: auto auto/ 500px 500px;
    justify-content: center;
    gap:5px;
    margin-top:30px;
}


.current-weather {
    display:grid;
    text-align: center;
    grid-template: repeat(4, 1fr)/auto auto; 

    font-size: 2rem;
    grid-column:1/2;
    grid-row:1/3;
}

#current-address, #current-temp, #current-condition {
    grid-column:1/3;
}

.hourly-weather {
    display:flex;
    flex-direction: row;
    max-width:100%;
    overflow: scroll;
    height:fit-content;
}

.hourly-weather div {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px black solid;
    min-width:100px;
}

.daily-weather {
    display: flex;
    flex-direction: column;
    max-width:100%;
}

.daily-weather div {
    display:flex;
    flex-direction: row;
    align-items: center;
    border: 1px black solid;
    justify-content: space-evenly;
    
